section {
    position: relative;
    width: 100%;
    height: 100%;
    /* 스크롤링된 요소가 스크롤 컨테이너의 시작 부분에 정렬되도록 지정 */
    scroll-snap-align: start
}

#container {
    /* 스크롤 요소의 스냅 기능을 설정함.  */
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
}

#container::-webkit-scrollbar {
    display: none;
}

.two {
    -webkit-mask-image: url('../../img/back.png');
    mask-image: url('../../img/back.png');
    mask-size: cover;
    width: 20rem;
    height: 30vh;
}

@media only screen and (max-width: 1328px) {
    .two {
        -webkit-mask-image: none;
        mask-image: none;
        background-color: #000;
        width: 2rem;
        height: 3vh;
        opacity: 0.1;
    }
    .value {
        margin-left: 5rem!important;
        margin-top: -42rem !important;
    }
    .talk {
        padding: 1rem;
        margin-left: 1rem !important;
        margin-top: 18rem !important;
        opacity: 0.8;
        color: #333;
    }
    .chan {
        padding: 1rem;
        margin-left: 1rem !important;
        margin-top: 31rem !important;
        color: #333;
        opacity: 0.8;
    }
    .Active {
        margin-top: -8rem !important;
        margin-bottom: 1rem !important;
    }
    .t {
        margin-top: -6rem;
    }
}

section::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 80%;
}

body::-webkit-scrollbar {
    display: none;
}

section h1 {
    position: absolute;
    top: 50%;
